  /* Style the button and place it in the middle of the container/image */
  .mi_contenedor_imagen .mi_quick_view {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    background-color: #000;
    color: white;
    font-size: 16px;
    padding: 12px 24px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    display:none;
  }
  
  .mi_contenedor_imagen .mi_quick_view:hover {
    background-color: #555;
  }
